/*city navigation wrapper */
.wrapper {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: var(--green);
  padding: calc(var(--index) * 1.5);
  border-radius: calc(var(--index) * 0.35);
}

/* item */
.cityItem {
  cursor: pointer;
  color: #fff;
  font-size: calc(var(--index) * 0.7);
  font-weight: 600;
}
/* active item */
.activeCityItem {
  cursor: pointer;
  padding: calc(var(--index) * 0.5);
  background-color: #fff;
  color: var(--green);
  font-size: calc(var(--index) * 0.7);
  font-weight: 600;
  border-radius: 5px;
}
@media (max-width: 767px) {
  .wrapper {
    min-height: 80vh;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    padding: 7px;
    border-radius: 0;
  }
  .cityItem {
    padding: 5px;
  }
  .activeCityItem {
    padding: 5px;
    border-radius: 3px;
  }
}
